type MyProps = {
  // using `interface` is also ok
  // message: string;
};
type MyState = {
  coverClass: string; // like this
};
class App extends React.Component<MyProps, MyState> {
  constructor(props: MyProps) {
    super(props);
    this.state = { coverClass: 'hidden' };
  }
  handleClick() {
    this.setState({ coverClass: 'cover' });
  }

  render() {
    return (
      <>
        <Header />
        <Main
          coverClass={this.state.coverClass}
          onClick={() => this.handleClick()}
        />
        <Footer />
      </>
    );
  }
}

export default App;
